import login2 from '@/web/login/login2';
export default [{
    path: '/',
    name: 'login2',
    meta: {
        requireAuth: true,
        showWater: true,
        isLogin: true,
        title: '正在登录..'
    },
    component: login2
    // component: resolve => require(['/web/login/login'], resolve)
}]


