<template>
    <div class="trsTextarea" id="trsTextarea" v-html="innerText" :contenteditable="canEdit" @focus="isLocked = true" @blur="isLocked = false" @input="changeText($event)" @keyup='keyUpText($event)'></div>
</template>
<script>
// import $ from '@/jquery-vendor.js'
export default {
    name: 'trsTextarea',
    props: {
        value: {
            type: String,
            default: ''
        },
        canEdit: {
            type: Boolean,
            default: true
        },
        trsType: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            innerText: this.value,
            isLocked: false
        }
    },
    watch: {
        'value' () {
            // || !this.innerText
            if (!this.isLocked) {
                this.innerText = this.value;
            }
        }
    },
    mounted() {
        // console.log(this);
    },
    methods: {
        changeText(event) {
            var vm = this;
            if (vm.trsType == true) {
                this.$emit('input', vm.removeHtml(vm.$el.innerHTML));
            } else {
                this.$emit('input', vm.$el.innerHTML);
            }
        },
        keyUpText(event) {
            var vm = this;
            if (event.code == 'KeyV') {
                vm.$el.innerHTML = vm.removeHtml(vm.$el.innerHTML);
            }
        }
    }
}
</script>
<style scoped>
.trsTextarea {
    overflow: auto;
    word-break: break-all;
    outline: none;
    user-select: text;
    white-space: pre-wrap;
    text-align: left;
    -webkit-tap-highlight-color: transparent;
}

.trsTextarea[contenteditable=true] {
    user-modify: read-write-plaintext-only;
}

.trsTextarea[contenteditable=true]:empty:before {
    content: attr(placeholder);
    display: block;
}







/*.trsTextarea:focus {
    content: none;
}*/
</style>


