let showWaterMark = function () {
    // 8月16号上线
    /**
     * 添加水印
     * @param：无，空json：{}
     */
    console.log('添加水印');
    // alert('showWater1');
    // 成功后回调函数
    var _success = function (ret) {
        // clearTimeout(myTime);
        // 验证效验码
        UPCHAT.M.NAPI.dismiss();
        if (ret.status == '0') {
            console.log(JSON.stringify(ret));
        }
    };

    // var myTime = setTimeout('alert("该接口只能在最新版U聊使用，请下载最新版U聊!")', 1000);

    // 失败后回调函数
    var _fail = function (fal) {
        // clearTimeout(myTime);
        UPCHAT.M.NAPI.dismiss();
    };
    UPCHAT.M.NAPI.showWaterMark(_success, _fail);
}
router.beforeEach((to, from, next) => {
    if (UPCHAT.M.isNative()) {
        UPCHAT.M.init(showWaterMark);
    }
    if (to.matched.some(r => r.meta.requireAuth)) {
        next();
    } else {
        // console.log($.cookie('iPlanetDirectoryPro'));
        if (window.localStorage.getItem('vuex') != null && ($.cookie('iPlanetDirectoryPro') != null || $.cookie('iPlanetDirectoryPro') != undefined)) {
            setTimeout(function () {
                next();
            }, 100);
        } else {
            next({
                path: '/',
                query: {
                    redirect: to.fullPath
                }
            })
        }
    }
})
export default router;


