import LoadingComponent from './loading/loading.vue';
import trsTextarea from './trsTextarea/trsTextarea.vue';
import secretaryAgent from './secretaryAgent/secretaryAgent.vue';
import '@/components/util/tools/tools';
const util = {
    install: function(Vue) {
        Vue.component('Loading', LoadingComponent);
        Vue.component('trsTextarea', trsTextarea);
        Vue.component('secretaryAgent', secretaryAgent);
    }
};

export default util;


