// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

import Vue from 'vue';
import axios from 'axios';
import $ from './jquery-vendor';
import 'jqueryCookie';

function toFormData(obj) {
    var query = '';
    var name, value, fullSubName, subName, subValue, innerObj, i;

    for (name in obj) {
        value = obj[name];

        if (value instanceof Array) {
            for (i = 0; i < value.length; ++i) {
                subValue = value[i];
                fullSubName = name; // name + '[' + i + ']';
                innerObj = {};
                innerObj[fullSubName] = subValue;
                query += toFormData(innerObj) + '&';
            }
        } else if (value instanceof Object) {
            for (subName in value) {
                subValue = value[subName];
                fullSubName = name + '[' + subName + ']';
                innerObj = {};
                innerObj[fullSubName] = subValue;
                query += toFormData(innerObj) + '&';
            }
        } else if (value !== undefined && value !== null) {
            query += encodeURIComponent(name) + '=' + encodeURIComponent(value) + '&';
        }
    }
    return query.length ? query.substr(0, query.length - 1) : query;
}

axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'; // 配置请求头
axios.defaults.transformRequest = [function(data) {
    return typeof data === 'object' && String(data) !== '[object File]' ? toFormData(data) : data;
}];
Vue.prototype.$http = axios;
Vue.prototype.post = function(url, params, successCallback, errorCallback, load = true) {
    var vm = this;
    if (load) {
        vm.$dialog.loading.open('加载中...');
    }
    params['random'] = Math.floor(Math.random() * (100000 + 1));
    axios.post(url, params).then(function(res) {
        return res;
    }).then(function(res) {
        if (res.data.isSuccess === true || res.data.isSuccess === 'true') {
            successCallback(res.data);
        } else if (res.data.isSuccess === false || res.data.isSuccess === 'false') {
            // vm.$dialog.toast({
            //     mes: res.data.errorMessage || '系统正在开小差，请稍后再试',
            //     timeout: 1500
            // });
            successCallback(res.data);
        } else if (res.data.isSuccess === undefined || res.data.isSuccess === 'undefined') {
            successCallback(res.data);
        }
        if (load) {
            vm.$dialog.loading.close();
        }
        // console.log('关闭loading');
    }, function(err) {
        if (load) {
            vm.$dialog.loading.close();
        }
        // console.log('error关闭loading');
        // vm.$dialog.toast({
        //     mes: '系统正在开小差，请稍后再试',
        //     timeout: 1500
        // });
        if (errorCallback && errorCallback instanceof Function) {
            errorCallback(err);
        }
    });
};

Vue.prototype.get = function(url, params, successCallback, errorCallback, load = true) {
    var vm = this;
    if (load) {
        vm.$dialog.loading.open('加载中...');
    }
    params['random'] = Math.floor(Math.random() * (100000 + 1));
    axios.get(url, {
        params: params
    }).then(function(res) {
        return res;
    }).then(function(res) {
        if (res.data.isSuccess === true || res.data.isSuccess === 'true') {
            successCallback(res.data);
        } else if (res.data.isSuccess === false || res.data.isSuccess === 'false' || res.isSuccess == 'false') {
            // vm.$dialog.toast({
            //     mes: res.data.errorMessage || '系统正在开小差，请稍后再试',
            //     timeout: 1500
            // });
            successCallback(res.data);
        } else if (res.data.isSuccess === undefined || res.data.isSuccess === 'undefined') {
            successCallback(res.data);
        }
        if (load) {
            vm.$dialog.loading.close();
        }
        // console.log('关闭loading' + url);
    }, function(err) {
        if (load) {
            vm.$dialog.loading.close();
        }
        // console.log('error关闭loading');
        // vm.$dialog.toast({
        //     mes: '系统正在开小差，请稍后再试',
        //     timeout: 1500
        // });
        if (errorCallback && errorCallback instanceof Function) {
            errorCallback(err);
        }
    });
};

