// 流程处理报错 OA_ERROR_0001 变换成 该流程已被处理 跳转到列表页面
Vue.prototype.getH = function(url, params, successCallback, errorCallback) {
    var vm = this;
    vm.$dialog.loading.open('加载中...');
    params['random'] = Math.floor(Math.random() * (100000 + 1));
    axios.get(url, {
        params: params
    }).then(function(res) {
        return res;
    }).then(function(res) {
        if (res.data.isSuccess === true || res.data.isSuccess === 'true') {
            successCallback(res.data);
        } else if (res.data.isSuccess === false || res.data.isSuccess === 'false' || res.isSuccess == 'false') {
            if (res.data.errorMessage == 'OA_ERROR_0001') {
                vm.$dialog.toast({
                    mes: '该流程已被处理',
                    timeout: 1500,
                    callback: () => {
                        setTimeout(function() {
                            vm.$router.push('/handle');
                        })
                    }
                });
            } else {
                // vm.$dialog.toast({
                //     mes: res.data.errorMessage || '系统正在开小差，请稍后再试',
                //     timeout: 1500
                // });
            }
        } else if (res.data.isSuccess === undefined || res.data.isSuccess === 'undefined') {
            successCallback(res.data);
        }
        vm.$dialog.loading.close();
        // console.log('关闭loading' + url);
    }, function(err) {
        vm.$dialog.loading.close();
        // console.log('error关闭loading');
        // vm.$dialog.toast({
        //     mes: '系统正在开小差，请稍后再试',
        //     timeout: 1500
        // });
        if (errorCallback && errorCallback instanceof Function) {
            errorCallback(err);
        }
    });
};

Vue.prototype.xml = function(url, params, successCallback, errorCallback) {
    var vm = this;
    vm.$dialog.loading.open('加载中...');
    params['random'] = Math.floor(Math.random() * (100000 + 1));
    $.ajax({
        type: 'get',
        url: url,
        data: params,
        dataType: 'xml',
        async: true,
        cache: false,
        timeout: 30000,
        success: function(dataJson) {
            successCallback(dataJson);
            vm.$dialog.loading.close();
        },
        error: function(errorThrown) {
            vm.$dialog.loading.close();
            // vm.$dialog.toast({
            //     mes: '系统正在开小差，请稍后再试',
            //     timeout: 1500
            // });
            errorCallback(errorThrown);
        }
    });
};


