<template>
</template>
<script>
import { mapMutations } from 'vuex';
import $ from '../../jquery-vendor';
import 'jqueryCookie';
export default {
    name: 'login',
    data() {
        return {
            noLogin: false,
            text: '123',
            sTime: 0,
            eTime: 0,
            empcode: 'liaowen'
        }
    },
    mounted() {
        document.setTitle('正在登录...');
        this.login();
    },
    methods: {
        ...mapMutations([
            'setUserInfo'
        ]),
        login() {
            var vm = this;
            var promise = new Promise(function(resolve, reject) {
                vm.uLogin(resolve);
            });
            let redirect = decodeURIComponent(this.$route.query.redirect || '/workCenter');
            promise.then(function(data) {
                if (data) {
                    setTimeout(function() {
                        console.log(1)
                        vm.$router.push({ path: redirect });
                    }, 100);
                }
            });
        },
        uLogin(resolve) {
            var vm = this;
            // 成功后回调函数
            var _success = function(result) {
                // if (window.history.length > 1) {
                //     UPCHAT.M.NAPI.closeWebView('');
                // }
                // 验证效验码
                vm.eTime = new Date().getTime();
                UPCHAT.M.NAPI.dismiss();
                if (result.status == '0') {
                    // 调用校验码验证接口
                    vm.get('/uchat/checkSecurity', {
                        'time': vm.eTime - vm.sTime,
                        'code': result.security
                    }, function(res) {
                        /**
                         * @params userinfo.userid
                         */
                        vm.setUserInfo({
                            role: res.role, // 0 普通员工 1 部门领导 2 公司领导
                            ssotokenCode: res.ssotoken,
                            isLeader: res.isLeader,
                            ssotoken: res.useridssotoken,
                            empcode: res.empcode,
                            deptname: res.deptname,
                            deptid: res.deptid,
                            userid: res.userid,
                            gender: res.gender,
                            orgname: res.orgname,
                            empname: res.empname,
                            orgId: res.orgId,
                            otel: res.otel
                        });
                        $.cookie('iPlanetDirectoryPro', res.useridssotoken, { expires: 1, path: '/' });
                        if (res.test) {
                            $.cookie('test', res.test, { path: '/' });
                        } else {
                            $.cookie('test', null);
                        }
                        resolve(true);
                    });
                }
            };
            // 失败后回调函数
            var _fail = function(fal) {
                UPCHAT.M.NAPI.dismiss();
                toApp();
            };
            // 获取效验码
            var getCheckCode = function() {
                UPCHAT.M.NAPI.showLoadingView();
                vm.sTime = new Date().getTime();
                UPCHAT.M.NAPI.getSecurity(_success, _fail, { 'sysid': 'upchat_team' });
            };
            // 非U聊客户端打开
            var toApp = function() {
                // remindMe
                vm.$router.push('/remindMe');
            };
            if (UPCHAT.M.isNative()) {
                UPCHAT.M.init(getCheckCode);
            } else {
                toApp();
            }
        }
    }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
a.test {
    color: red;
}
</style>


