<template>
    <yd-layout>
        <yd-navbar bgcolor="#70b6ef" title="测试登录页面" color="#fff" fontsize="0.36rem">
            <router-link to="/" slot="left">
                <yd-navbar-back-icon color="#fff">首页</yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
        <yd-cell-group>
            <yd-cell-item>
                <span slot="left">用户名：</span>
                <yd-input slot="right" @keyup.enter.native='login()' v-model="empcode" placeholder="请输入用户名"></yd-input>
            </yd-cell-item>
        </yd-cell-group>
        <yd-radio-group v-model="empcode">
            <yd-radio val="liaowen">liaowen</yd-radio>
            <yd-radio val="zhangjunfeng">zhangjunfeng</yd-radio>
            <yd-radio val="tianjing">tianjing</yd-radio>
            <yd-radio val="renhaiying">renhaiying</yd-radio>
            <yd-radio val="huying">领导:huying</yd-radio>
            <yd-radio val="yuxiaobin">领导:yuxiaobin</yd-radio>
            <yd-radio val="liwei1">领导:liwei1</yd-radio>
            <yd-radio val="songhanshi">领导:songhanshi</yd-radio>
            <yd-radio val="xujianan">xujianan</yd-radio>
        </yd-radio-group>
        <yd-button size="large" type="primary" @click.native='login()'>登录</yd-button>
    </yd-layout>
</template>
<script>
import { mapMutations } from 'vuex';
import $ from '../../jquery-vendor';
import 'jqueryCookie';
export default {
    name: 'login2',
    data() {
        return {
            text: '123',
            sTime: 0,
            eTime: 0,
            empcode: 'liaowen'
        }
    },
    mounted() {
        document.setTitle('测试登录页面');
        // this.login();
        // screenLog.init();
    },
    methods: {
        ...mapMutations([
            'setUserInfo'
        ]),
        login() {
            var vm = this;
            var promise = new Promise(function(resolve, reject) {
                vm.get(vm.conf.url + '/uchat/backGate', {
                    'userid': vm.empcode
                }, function(res) {
                    vm.setUserInfo({
                        role: res.role, // 0 普通员工 1 部门领导 2 公司领导
                        ssotokenCode: res.ssotoken,
                        isLeader: res.isLeader,
                        ssotoken: res.useridssotoken,
                        empcode: res.empcode,
                        deptname: res.deptname,
                        deptid: res.deptid,
                        userid: res.userid,
                        gender: res.gender,
                        orgname: res.orgname,
                        empname: res.empname,
                        orgId: res.orgId,
                        otel: res.otel
                    }, function(res) {
                        if (res.response.status == 502) {
                            vm.login();
                        }
                    });
                    if (res.isSuccess != 'false') {
                        $.cookie('iPlanetDirectoryPro', res.useridssotoken, { expires: 1, path: '/' });
                        if (res.test) {
                            $.cookie('test', res.test, { path: '/' });
                        } else {
                            $.cookie('test', null);
                        }
                        vm.$dialog.toast({
                            mes: '登录成功!',
                            timeout: 1500
                        });
                        resolve(true);
                    }
                });
                // vm.uLogin(resolve);
            });
            let redirect = decodeURIComponent(this.$route.query.redirect || '/workCenter');
            // var startNewWebView = function (url, ext) {
            //     UPCHAT.M.NAPI.startNewWebView(url);
            // }
            promise.then(function(data) {
                if (data) {
                    setTimeout(function() {
                        /**
                         *  U聊跳新页面
                         */
                        // if (UPCHAT.M.isNative()) {
                        //     UPCHAT.M.init(startNewWebView(`http://172.18.248.41/${redirect}`, {}));
                        // }
                        vm.$router.push({ path: redirect });
                    }, 100);
                }
            });
        },
        uLogin(resolve) {
            var vm = this;
            // 成功后回调函数
            alert(1)
            var _success = function(result) {
                // if (window.history.length > 1) {
                //     UPCHAT.M.NAPI.closeWebView('');
                // }
                // 验证效验码
                UPCHAT.M.NAPI.dismiss();
                if (result.status == '0') {
                    // 调用校验码验证接口
                    alert(2)
                    alert(result.security);
                    vm.get('/uchat/checkSecurity', {
                        'code': result.security
                    }, function(res) {
                        vm.setUserInfo({
                            role: res.role, // 0 普通员工 1 部门领导 2 公司领导
                            ssotokenCode: res.ssotoken,
                            isLeader: res.isLeader,
                            ssotoken: res.useridssotoken,
                            empcode: res.empcode,
                            deptname: res.deptname,
                            deptid: res.deptid,
                            userid: res.userid
                        });
                        $.cookie('iPlanetDirectoryPro', res.useridssotoken, { expires: 1, path: '/' });
                        alert('ssotokenCode' + res.ssotoken)
                        alert('isLeader', res.isLeader);
                        alert('ssotoken' + res.useridssotoken)
                        alert('empcode' + res.empcode)
                        alert('deptname' + res.deptname)
                        alert('deptid' + res.deptid)
                        alert('userid' + res.userid)
                        resolve(true);
                    });
                }
            };
            // 失败后回调函数
            var _fail = function(fal) {
                UPCHAT.M.NAPI.dismiss();
                toApp();
            };
            // 获取效验码
            var getCheckCode = function() {
                UPCHAT.M.NAPI.showLoadingView();
                UPCHAT.M.NAPI.getSecurity(_success, _fail);
            };
            // 非U聊客户端打开
            var toApp = function() {
                // remindMe
                vm.$router.push('/remindMe');
            };
            if (UPCHAT.M.isNative()) {
                UPCHAT.M.init(getCheckCode);
            } else {
                toApp();
            }
        }
    }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
a.test {
    color: red;
}
</style>


